.componentWrapper{
    
    border: 1px solid rgb(0, 0, 0);
    box-sizing: border-box;
    padding: 5px;
    border-radius: 15px;
    background-color: #2b2b2b;
    width: 100%;
}
.option {
    background-color: #616161;
    border: 1px solid rgb(0, 0, 0);
    box-sizing: border-box;
    padding: 15px;
}

.scrollableContent{
    overflow: scroll;
}